.header {
    grid-area: header;
    background: #fff url(../../img/timeline-1.png) no-repeat center;
    background-size: cover;
    height: 400px;
    margin-bottom: 30px;
}

.container {
    display: flex;
    align-items: flex-end;
    max-width: 1200px;
    margin: 0 auto;
    height: 100%;
}

.headerInner {
    display: flex;
    align-items: center;
    justify-content: start;
    margin-bottom: 50px;
}

.userAvatar img {
    height: 170px;
    width: 170px;
    border-radius: 50% ;
    border: solid white 5px;
}

.btn {
    margin-left: 20px;
    padding: 10px 15px;
    background-color: #000;
    border: none;
    color: #fff;
    transition: background-color .5s linear;
}

.btn:hover {
    background-color: transparent;
    cursor: pointer;
}